@charset "UTF-8";

/* サイトを開いた時の処理 */

.index {
    .start {
        background: #FFF;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 9000;
        height: 100%;
        width: 100%;
    }
    .start h1 {
        /* background-color: red; */
        margin: 0 auto;
        text-align: center;
        font-size: 50px;
        font-family: cursive;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        display: none;
        z-index: 9999;
        width: 100%;
    }
    /* サイトを開いた時の処理終了 */
    main {
        height: 80%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        box-sizing: border-box;
    }
    main h1 {
        font-size: 60px;
    }
    .text-box {
        display: flex;
        flex-direction: column;
        text-align: center;
        justify-content: space-around;
        /* position: relative; */
        /* top: -10px; */
        width: 50%;
        height: 100%;
    }
    .text-box section {
        margin: 0 auto;
        display: block;
        margin-bottom: 40px;
        color: black;
        font-family: cursive;
        background-color: white;
        width: 80%;
        height: 140px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 5%;
    }
    #message {
        margin: 0 auto;
        width: 75%;
        font-size: 17px;
        font-weight: bold;
        line-height: 40px;
        background-color: rgba(253, 253, 253, 0.5);
        border-radius: 6%;
        margin-top: 80px;
    }
    #message a {
        color: green;
    }
    .img-box {
        position: relative;
        width: 40%;
        left: 10%;
        height: 100%;
    }
    #izena {
        width: 45%;
        position: absolute;
        left: 20%;
        top: 100px;
        display: none;
        border-radius: 100%;
    }
    #sea {
        width: 40%;
        position: absolute;
        /* left:400px; */
        left: 65%;
        top: 320px;
        display: none;
    }
    #hibiscus {
        position: absolute;
        width: 50%;
        top: -30px;
        left: -20%;
        display: none;
    }
    #aerial {
        position: absolute;
        width: 50%;
        top: 300px;
        left: -20%;
        display: none;
    }
    #yuhi {
        position: absolute;
        width: 50%;
        bottom: 450px;
        right: -10%;
        display: none;
    }
    main img {
        width: 100%;
    }
    .img-box img {
        margin-top: 100px;
        border-radius: 100%;
    }
    footer {
        height: 35px;
        display: flex;
        justify-content: center;
        align-items: center;
        /* background: rgba(255,255,255,0.5); */
    }
    footer p {
        font-size: 20px;
        /* margin-left: 200px; */
    }
    /* ボタンの挙動 */
    .button a {
        color: black;
        font-size: 60px;
        font-weight: 900;
    }
    .cover {
        font-size: 30px;
    }
    .outie {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .outie i {
        font-size: 35px;
        margin-right: 10px;
    }
    .outie i:last-child {
        margin-right: 0;
    }
    /* ボタンの挙動終了 */
}